import { createSlice } from '@reduxjs/toolkit'
import Cookies from 'js-cookie';
export const installSlice = createSlice({
    name: 'install',
    initialState: {
        value: JSON.parse(Cookies.get('roobi_install') || null) || null,
    },
    reducers: {
        modeUpdata: (state, action) => {
            state.value = action.payload
            Cookies.set('roobi_install', JSON.stringify(action.payload));
        },
    },
})

export const { modeUpdata } = installSlice.actions

export default installSlice.reducer