import { createSlice } from '@reduxjs/toolkit'
import Cookies from 'js-cookie';
export const networkSlice = createSlice({
    name: 'networkData',
    initialState: {
        value: JSON.parse(Cookies.get('roobi_networkData') || null) || [],
    },
    reducers: {
        changeNetworkData: (state, action) => {
            state.value = action.payload
            Cookies.set('roobi_networkData', JSON.stringify(action.payload));
        },
    },
})

export const { changeNetworkData } = networkSlice.actions

export default networkSlice.reducer