import React, { useState, useEffect } from 'react'

import { FormattedMessage } from 'react-intl';
import { useSelector } from 'react-redux';
import { IntlProvider } from 'react-intl';
import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';

import { useNavigate } from 'react-router-dom';

import logo from '../../static/palmshell-logo.svg'
import close from '../../static/clear.svg'
import { Radio } from 'antd';

import { get_env, hasPermission } from '../../api'
import { message } from 'antd'

import './index.css'
import SocketElement from '../../components/SocketElement';

export default () => {
    const go = useNavigate()
    const [protocols, setProtocols] = useState(false)
    const [nextFalse, setNextFalse] = useState(false)

    const lang = useSelector((state) => state.lang.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const [nextUrl, setNextUrl] = useState(null)


    const styles = {
        backgroundColor: '#959595',
        borderColor: '#959595',
    }

    useEffect(() => {
        hasPermission().then(res => {
            if (res.data.permission) {
                setNextUrl('/networkTesting')
            } else {
                get_env().then(res => {
                    if (res.data.env === 'remote') {
                        setNextUrl('/accreditation')
                    } else if (res.data.env === 'local') {
                        console.log(res.data.token);
                        localStorage.setItem('roobi_token', res.data.token)
                        setNextUrl('/networkTesting')
                    }
                }).catch(err => {
                    message.error('System Error', [2]);
                })
            }
        }).catch(err => {
            message.error('System Error', [2]);
        })

    }, [])

    const startFn = () => {
        go(nextUrl)
    }

    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <SocketElement />
            <div className='content'>
                <div className='content_contexts'>
                    <img src={logo} />
                    <div className='protocols_mark' style={{ display: `${protocols ? 'block' : 'none'}` }}>
                        <div>
                            <img src={close} className='close' onClick={() => { setProtocols(false) }} />
                            <h1>Policy</h1>
                            <p>Welcome to use ROOBI, the radxa out of box installer, we hope to make your Installation experience easy and smooth. Make sure the following during the</p>
                        </div>
                    </div>
                </div>
                <a className='next start_a' style={!nextFalse ? styles : null} onClick={!nextFalse ? null : startFn}> <FormattedMessage id='start'></FormattedMessage> </a>
                <div className='flex_text'>
                    <Radio
                        onChange={(value) => {
                            setNextFalse(value.target.checked);
                        }}
                    ></Radio>
                    <p onClick={() => { setProtocols(f => !f) }}> <FormattedMessage id='agreement'></FormattedMessage></p>
                </div>
            </div>
        </IntlProvider>

    )
}

