import React, { useState, useEffect, useRef } from 'react'
import { socket } from '../../api/websocket';
import { useDispatch, } from 'react-redux';
import { modeUpdata } from '../../utils/installSlice'

import { useNavigate } from 'react-router-dom';

export default () => {
    const dispatch = useDispatch()
    const go = useNavigate()

    // 监听socket消息
    useEffect(() => {
        console.log(socket);
        if (socket) {
            socket.on('respond', function (data) {

                let msg = data.type === 'install' || data.type === 'images'
                if (msg) {
                    if (window.location.pathname !== '/mounting' && window.location.pathname !== '/accreditation' && window.location.pathname !== '/success' && window.location.pathname !== '/medium') {
                        console.log('跳转');
                        // window.location.href = '/mounting'
                        go('/mounting');
                    }
                }

                dispatch(modeUpdata(data))
            });
        }
    }, [])


    return <div style={{ display: 'none' }}></div>
}