import { createSlice } from '@reduxjs/toolkit'
import Cookies from 'js-cookie';
export const downSlice = createSlice({
    name: 'down',
    initialState: {
        value: JSON.parse(Cookies.get('roobi_down') || null) || '',
    },
    reducers: {
        downData: (state, action) => {
            state.value = action.payload
            Cookies.set('roobi_down', JSON.stringify(action.payload));
        },
    },
})

export const { downData } = downSlice.actions

export default downSlice.reducer