import { io } from 'socket.io-client';

export let socket = null;
const namespace = '/websocket';
// const websocketUrl = 'http://192.168.2.74:739/websocket'
// const websocketUrl = 'http://192.168.2.184:739/websocket'
const websocketUrl = window.location.protocol + '//' + '192.168.2.198:739' + namespace
// const websocketUrl = window.location.protocol + '//' + window.location.host + namespace
// console.log("test", window.location.host);

export function CreateSocket() {
    var skid = '';

    if (socket !== null) return;
    console.log('create socket');

    // 创建socket连接
    socket = io.connect(websocketUrl);

    // 建立连接事件
    socket.on('connect', function (data) {
        console.log('connect:' + data);
        skid = socket.id;
        localStorage.setItem('roobi_skid', skid);
        // console.log('skid', skid);
    })

    // 建立断开事件
    socket.on('disconnect', function (data) {
        // console.log('disconnect:' + data);
    });

    // 监听事件
    socket.on('respond', function (data) {
        console.log(data);
        // let msg = data.type === 'install' || data.type === 'images'
        // if (msg) {
        //     if (window.location.pathname !== '/mounting' && window.location.pathname !== '/accreditation' && window.location.pathname !== '/success' && window.location.pathname !== '/medium') {
        //         console.log('跳转');
        //         // window.location.href = '/mounting'
        //         window.history.pushState(null, '', '/mounting');
        //     }
        // }
    });

    return skid
}