import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import './index.css'
import { useSelector, useDispatch } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import { IntlProvider } from 'react-intl';


import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';

import { modeUpdata } from '../../utils/installSlice'
import Head from '../../components/Head';
import { get_token } from '../../api'
import { socket } from '../../api/websocket';
import tips_image from '../../static/button-tips.webp'

export default () => {
    const lang = useSelector((state) => state.lang.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const go = useNavigate()
    const dispatch = useDispatch()
    const [count, setCount] = useState(null);
    const [dis, setDis] = useState(false)
    const [msg_err, setMsg_err] = useState(null)

    const tokenFn = () => {
        setDis(true)
        setMsg_err('')
        let sid = localStorage.getItem('roobi_skid')

        get_token({ sid }).then(res => {
            console.log(res.data);
            if (res.data.msg === 'success') {
                setCount(60)
            } else {

            }
        }).catch(err => {
            if (err.response.data.errcode === 13) {
                setDis(false)
                setMsg_err('Please retry after 5 minutes')
            }
        })
    }

    useEffect(() => {
        let intervalId;
        const decrementCount = () => {
            setCount((prevCount) => prevCount - 1);
        };

        if (count > 0) {
            intervalId = setInterval(decrementCount, 1000);
        } else {
            setDis(false)
            setMsg_err('')
        }

        return () => {
            clearInterval(intervalId);
        };
    }, [count]);
    // 监听socket消息
    useEffect(() => {
        if (socket) {
            socket.on('respond', function (data) {
                dispatch(modeUpdata(data))
                if (data) {
                    if (data.type === 'authentication') {
                        if (data.status === 'success') {
                            localStorage.setItem('roobi_token', data.token)
                            // message.success('success', [2], () => {
                            if (localStorage.getItem('roobi_back_url')) {
                                go(localStorage.getItem('roobi_back_url'))
                            }
                            go('/networkTesting')
                            // });
                        } else if (data.status === 'error') {
                            // message.error('error', [2]);
                        }
                    } else if (data.type === 'install' || data.type === 'image') {
                        // go('/mounting')
                    }
                }
            });
        }
    }, [])


    const styles = {
        textAlign: 'center',
        fontSize: '20px',
        marginBottom: '20px',
    }

    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <div className='content'>
                <Head />
                <div className='network_box gettoken' style={{ backgroundColor: '#fff' }}>
                    <h1 style={styles}>Since you are accessing the device remotely, you need to authenticate.<br></br>
                        Press the power button three times to complete the authentication.</h1>
                    <p className='start_p'>{msg_err}</p>
                    {
                        dis ? <h1 className='start_h1'>{count}</h1> : <div className='start_' onClick={tokenFn}>{msg_err ? <FormattedMessage id='retry' /> : <FormattedMessage id='start' />}</div>
                    }

                    <img src={tips_image} className='env_image' />
                </div>
            </div>
        </IntlProvider >
    )
}
