import React, { useState, useEffect, useRef } from 'react'

import { FormattedMessage } from 'react-intl';
import { useSelector, useDispatch, } from 'react-redux';
import { IntlProvider } from 'react-intl';
import enLoacleData from '../../locales/en';
import zhLoacleData from '../../locales/zh-CN';
import { useLocation } from 'react-router-dom'
import Progress from '../../components/Progress';
import mounting from '../../static/mounting.svg'

import { ConfigProvider, Modal, Spin } from 'antd';

import { useNavigate } from 'react-router-dom';
import './index.css'

import { pauseInstall, continueInstall, stopInstall } from '../../api'

import { socket } from '../../api/websocket';
import { modeUpdata } from '../../utils/installSlice'
import SocketElement from '../../components/SocketElement';


export default () => {
    const lang = useSelector((state) => state.lang.value);
    const downMsg = useSelector((state) => state.install.value);
    const messages = lang === 'en' ? enLoacleData : zhLoacleData;
    const go = useNavigate()
    const dispatch = useDispatch()
    const location = useLocation();

    useEffect(() => {
        console.log('指向');
    }, [location.pathname]);

    const [isModalOpen, setIsModalOpen] = useState(false);

    // 进度
    const [num, setNum] = useState(0)
    // 下载进度文字
    const [downText, setDownText] = useState('')

    // 标题
    const [title, setTitle] = useState('')
    // 状态
    const [downMode, setDownMode] = useState(0)
    // stop
    const [stop, setStop] = useState(false)
    // 当前状态
    const [mode, setMode] = useState(0)

    // 日志
    const [log, setLog] = useState(null)

    const [typeText, setTypeText] = useState('')


    // 开始
    const downloadStart = () => {

    }
    // 暂停
    const downloadPause = () => {
        setDownMode(2)
        pauseInstall().then(res => {
            console.log('暂停', res.data);
        }).catch(err => console.log(err))
    }
    // 继续
    const downloadContinue = () => {
        setDownMode(1)
        continueInstall().then(res => {
            console.log('继续', res.data);
        }).catch(err => console.log(err))
    }
    // 取消
    const downloadStop = () => {
        setStop(true)
        setDownMode(0)
        setMode(0)
        stopInstall().then(res => {
            setMode(0)
            setIsModalOpen(false)
            setStop(false)
            setNum(0)
            setTitle(' ')
            setDownText(' ')
            console.log('取消', res.data);
        }).catch(err => {
            setIsModalOpen(false)
            console.log(err)
        })
    }

    // 重新选择镜像
    const selectImage = () => {
        go('/image-select')
    }

    useEffect(() => {
        if (downMsg) {
            if (downMsg.type === 'images') {
                setTitle(downMsg.title)
                if (downMsg.status === 'running') {
                    setDownMode(1)
                    setMode(0)
                    setNum(parseInt(downMsg.percent.toFixed(2) * 100))
                    setDownText(downMsg.msg)
                    setTypeText(<FormattedMessage id='downloading' />)
                    if (downMsg.percent < 0) {
                        setMode(1)
                    }

                } else if (downMsg.status === 'checking') {
                    setMode(1)
                    setTitle('Checking...')
                } else if (downMsg.status === 'pause') {
                    setDownMode(2)
                    setTypeText('Paused')
                } else if (downMsg.status === 'resume') {
                    setDownMode(1)
                } else if (downMsg.status === 'stop' || downMsg.status === 'checker_error') {
                    go('/failure', { replace: true })
                } else if (downMsg.status === 'connecting') {
                    setMode(1)
                    setDownText(downMsg.msg)
                    setDownMode(1)
                    // setTypeText('Connecting')
                }
            } else if (downMsg.type === 'install') {
                setTitle(downMsg.title)
                if (downMsg.status === 'running') {
                    setDownMode(1)
                    setMode(2)
                    setNum(parseInt(downMsg.percent.toFixed(2) * 100))
                    setDownText(downMsg.msg)
                    setTypeText(<FormattedMessage id='installation' />)
                    if (downMsg.percent < 0) {
                        setMode(1)
                    }

                } else if (downMsg.status === 'pause') {
                    setDownMode(2)
                    setTypeText('Paused')
                } else if (downMsg.status === 'resume') {
                    setDownMode(1)
                } else if (downMsg.status === 'stop') {
                    go('/failure', { replace: true })

                } else if (downMsg.status === 'reboot') {
                    setNum(100)
                    go('/success', { replace: true })

                } else if (downMsg.status === 'resume') {


                }
            }
        }
    }, [downMsg])

    const StartElement = () => {
        return <>
            <div className='download'>
                <h2>{title} {typeText}</h2>
                <div className='download_line'>
                    <div className='line'>
                        <div style={{ width: `${num}%` }}></div>
                    </div>
                    <p>{num + '%'}</p>
                </div>
                <p>{downText}</p>
            </div>
        </>
    }

    const CheckingElement = () => {
        return <>
            <div className='download'>
                <h2>{title} {typeText}</h2>
                <div className='download_line'>
                    <div className='progress-bar '>
                        <div className='progress '>
                            <div></div>
                        </div>
                    </div>
                </div>
                <p>{downText}</p>
            </div>
        </>
    }


    const ImageInstall = () => {
        const logRef = useRef()
        useEffect(() => {
            const messagesContainer = logRef.current;
            messagesContainer.scrollTop = messagesContainer.scrollHeight;
        }, [log])
        return <div className='download'>
            <h2>{title} {typeText}</h2>
            <div className='download_line'>
                <div className='line'>
                    <div style={{ width: `${num}%` }}></div>
                </div>
                <p>{num + '%'}</p>
            </div>
            <p>{downText}</p>
            <div ref={logRef} className='log_box'>{log}</div>
        </div>
    }

    const elements = [
        { text: '下载中', ele: <StartElement /> },
        { text: '检查中', ele: <CheckingElement /> },
        { text: 'Installed', ele: <ImageInstall /> },
    ]

    const styles = {
        backgroundColor: '#959595',
        borderColor: '#959595',
        cursor: 'not-allowed',
        color: '#fff',
    }

    const type = [
        { text: <FormattedMessage id='start' />, method: downloadStart, svg: null },
        {
            text: <FormattedMessage id='pause' />,
            method: downloadPause,
            svg: <svg t="1697020690072" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4006" width="24" height="24"><path d="M512 1024C228.266667 1024 0 795.733333 0 512S228.266667 0 512 0s512 228.266667 512 512-228.266667 512-512 512z m0-42.666667c260.266667 0 469.333333-209.066667 469.333333-469.333333S772.266667 42.666667 512 42.666667 42.666667 251.733333 42.666667 512s209.066667 469.333333 469.333333 469.333333z m-106.666667-682.666666c12.8 0 21.333333 8.533333 21.333334 21.333333v384c0 12.8-8.533333 21.333333-21.333334 21.333333s-21.333333-8.533333-21.333333-21.333333V320c0-12.8 8.533333-21.333333 21.333333-21.333333z m213.333334 0c12.8 0 21.333333 8.533333 21.333333 21.333333v384c0 12.8-8.533333 21.333333-21.333333 21.333333s-21.333333-8.533333-21.333334-21.333333V320c0-12.8 8.533333-21.333333 21.333334-21.333333z" fill="#0c929c" p-id="4007"></path></svg>
        },
        {
            text: <FormattedMessage id='continue' />,
            method: downloadContinue,
            svg: <svg t="1697020788777" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="14295" width="24" height="24"><path d="M512 0C230.4 0 0 230.4 0 512s230.4 512 512 512 512-230.4 512-512S793.6 0 512 0z m0 964.266667C264.533333 964.266667 59.733333 759.466667 59.733333 512S264.533333 59.733333 512 59.733333 964.266667 264.533333 964.266667 512 759.466667 964.266667 512 964.266667z" fill="#0c929c" p-id="14296"></path><path d="M499.2 268.8c-17.066667-17.066667-42.666667-17.066667-59.733333 0s-17.066667 42.666667 0 59.733333l183.466666 183.466667-183.466666 183.466667c-17.066667 17.066667-17.066667 42.666667 0 59.733333 8.533333 8.533333 21.333333 12.8 29.866666 12.8s21.333333-4.266667 29.866667-12.8l213.333333-213.333333c17.066667-17.066667 17.066667-42.666667 0-59.733334l-213.333333-213.333333z" fill="#0c929c" p-id="14297"></path></svg>
        },
        { text: <FormattedMessage id='re-select' />, method: selectImage, svg: null },
        { text: <FormattedMessage id='Check in progress' />, method: null, styles, svg: null },
        { text: <FormattedMessage id='installation' />, method: null, styles, svg: null },
        { text: <FormattedMessage id='completed' />, method: () => { go('/success') }, svg: null },
    ]
    return (
        <IntlProvider locale={lang} messages={messages} key={lang}>
            <ConfigProvider theme={{
                token: {
                    colorPrimary: '#329fa7',
                },
            }}>
                <SocketElement />
                <Modal title='Cancel installation?' open={isModalOpen} onOk={stop ? null : downloadStop} onCancel={stop ? null : () => { setIsModalOpen(false) }} centered={true} okText='Confirm' cancelText='Cancel' okType="danger">
                    {
                        stop ? <Spin /> : <h2>< FormattedMessage id='cti' /></h2>
                    }
                </Modal>
                <div className='content'>
                    <Progress index='4' />
                    <div className='network_box'>
                        <div className='setting_box'>
                            <div className='head_icon'>
                                <img src={mounting} className='pc_icon' />
                                <h2><FormattedMessage id='down_title' /></h2>
                            </div>
                            {
                                elements[mode].ele
                            }
                        </div>
                    </div>
                    <div className='btns'>
                        <div onClick={() => { setIsModalOpen(true) }} ><FormattedMessage id='cancel' /></div>
                        <div onClick={type[downMode].method}>
                            {
                                type[downMode].text
                            }
                            {
                                type[downMode].svg
                            }
                        </div>
                    </div>
                </div>
            </ConfigProvider>
        </IntlProvider >
    )
}